@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  /* background-color: black; */
  background-image: url("/portfolio/static/media/background(2).228ff547.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  margin-bottom: 5px;
  font-family: 'Lora', serif;
}

.App-content {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  background-color: black;
  height: 100vh;
}

.row {
  width: 100%;
}

.col {
  display: inline-flexbox;
}

.right {
  margin-top: 8%;
  margin-left: 15%;
  text-align: left;
  /* color: #d57c8c; */
  color: rgb(252, 228, 212);
  float: right; 
  width: 49%;
  vertical-align: top;
}

.left {
  float: left;
  width: 30%;
  height: 30%;
  padding-left: 4%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.nav {
  width: 100%;
  /* background-color: #d57c8c; */
  background-color: rgb(252, 228, 212);
  height: 40px;
}

.scroller {
  color: rgb(252, 228, 212);
  background-color: black;
}

.projects {
  margin: 0 calc(40vmin);
  justify-content: center;
  color: rgb(54, 31, 31);
}

.contact {
  justify-content: center;
  color: rgb(54, 31, 31);
  height: 75vh
}

img {
  max-width: 400px;
}

a {
  color: black; 
  padding: 10px; 
}

.idOdd {
  background-color: rgb(252, 228, 212);
  margin: 100px; 
}

.idEven{
  background-color: red;
}

.projRow {
  width: 100%;
  /* background-color: rgb(252, 228, 212); */
  /* padding-right: 100px; */
  border: black;
  border-style: solid; 
}

.projCol {
  display: inline-flexbox;
  width: 28%;
  float: left;
  height: 80vh;
  padding-top: 10vh;
  /* border: black;
  border-style: solid;  */
  margin-right: 10%;
  margin-left: 10%;
  margin-top: 10%;
  margin-bottom:10%;
}

.box {
  /* border: black;
  border-style: solid;  */
  width: 30vw; 
  height: 60vh;
  padding: 80px;
  margin: 100px;
  float: center;
  text-align: left;
  justify-content: space-between;
  /* background-color: rgb(252, 228, 212); */
}
